// ============================================
// SIXORBIT UI - CSS RESET
// Normalize browser defaults
// ============================================

@use "../abstracts" as *;

// ============================================
// BOX SIZING
// ============================================

*,
*::before,
*::after {
  box-sizing: border-box;
}

// ============================================
// ROOT & BODY
// ============================================

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  @include custom-scrollbar(8px, $so-grey-400, transparent);
}

body {
  margin: 0;
  padding: 0;
  font-family: $so-font-family;
  font-size: font-size("base");
  font-weight: font-weight("regular");
  line-height: line-height("normal");
  color: css-var("text-primary");
  background-color: css-var("page-bg");
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// ============================================
// TYPOGRAPHY
// ============================================

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: font-weight("medium");
  line-height: line-height("tight");
  color: css-var("text-heading");
}

p {
  margin: 0 0 spacing("4");

  &:last-child {
    margin-bottom: 0;
    margin-top: 10px;
  }
}

// ============================================
// LINKS
// ============================================

a {
  color: css-var("accent-primary");
  text-decoration: none;
  background-color: transparent;
  transition: color $so-transition-base;

  &:hover {
    color: css-var("accent-primary-hover", $so-primary-hover);
  }
}

// ============================================
// LISTS
// ============================================

ol,
ul {
  margin: 0;
  padding: 0;
}

// ============================================
// HORIZONTAL RULE
// ============================================

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  border: 0;
  border-top: 1px solid css-var("border-color");
  margin: spacing("4") 0;
}

// ============================================
// CODE
// ============================================

pre,
code,
kbd,
samp {
  font-family: $so-font-family-mono;
  font-size: 1em;
}

code {
  padding: 0.2em 0.4em;
  font-size: 87.5%;
  background-color: alpha($so-grey-500, 0.1);
  border-radius: radius("sm");
}

pre {
  margin: 0;
  overflow: auto;
  font-size: 87.5%;

  code {
    padding: 0;
    font-size: inherit;
    background-color: transparent;
    border-radius: 0;
  }
}

// ============================================
// IMAGES & MEDIA
// ============================================

img,
svg {
  vertical-align: middle;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
}

// ============================================
// MATERIAL ICONS
// ============================================

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  vertical-align: middle;
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
}

// ============================================
// TABLES
// ============================================

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  text-align: inherit;
  text-align: -webkit-match-parent;
}

// ============================================
// FORMS
// ============================================

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: spacing("2");
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

// ============================================
// MISC
// ============================================

[hidden] {
  display: none !important;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
}

b,
strong {
  font-weight: font-weight("bold");
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

// ============================================
// SELECTION
// ============================================

::selection {
  background-color: alpha($so-primary, 0.2);
  color: inherit;
}

// ============================================
// FOCUS VISIBLE
// ============================================

:focus:not(:focus-visible) {
  outline: none;
}

:focus-visible,
.#{$so-prefix}focus-visible {
  outline: 2px solid css-var("accent-primary");
  outline-offset: 2px;
}
