@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  scrollbar-width: thin;
  scrollbar-color: #bdc1c6 transparent;
}
html::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: #bdc1c6;
  border-radius: 4px;
}
html::-webkit-scrollbar-thumb:hover {
  background: rgb(168.2341463415, 173.4926829268, 180.0658536585);
}

body {
  margin: 0;
  padding: 0;
  font-family: "Google Sans", "Google Sans Text", "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.4;
  color: var(--so-text-primary);
  background-color: var(--so-page-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 500;
  line-height: 1.2;
  color: var(--so-text-heading);
}

p {
  margin: 0 0 16px;
}
p:last-child {
  margin-bottom: 0;
  margin-top: 10px;
}

a {
  color: var(--so-accent-primary);
  text-decoration: none;
  background-color: transparent;
  transition: color 0.15s ease;
}
a:hover {
  color: var(--so-accent-primary-hover, #1557b0);
}

ol,
ul {
  margin: 0;
  padding: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  border: 0;
  border-top: 1px solid var(--so-border-color);
  margin: 16px 0;
}

pre,
code,
kbd,
samp {
  font-family: "Roboto Mono", "SF Mono", "Monaco", "Consolas", monospace;
  font-size: 1em;
}

code {
  padding: 0.2em 0.4em;
  font-size: 87.5%;
  background-color: rgba(154, 160, 166, 0.1);
  border-radius: 3px;
}

pre {
  margin: 0;
  overflow: auto;
  font-size: 87.5%;
}
pre code {
  padding: 0;
  font-size: inherit;
  background-color: transparent;
  border-radius: 0;
}

img,
svg {
  vertical-align: middle;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
}

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  vertical-align: middle;
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  text-align: inherit;
  text-align: -webkit-match-parent;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 8px;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[hidden] {
  display: none !important;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
}

b,
strong {
  font-weight: 700;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

::selection {
  background-color: rgba(26, 115, 232, 0.2);
  color: inherit;
}

:focus:not(:focus-visible) {
  outline: none;
}

:focus-visible,
.so-focus-visible {
  outline: 2px solid var(--so-accent-primary);
  outline-offset: 2px;
}

