  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 36px;
  height: 36px;
  margin-top: -18px;
  margin-left: -18px;
  background: currentColor;
  border-radius: 50%;
  opacity: 0;
  transform: scale(0);
  pointer-events: none;
  transition: none;
}
