.so-toast-container {
  position: fixed;
  z-index: 1080;
  padding: 16px;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-height: 100vh;
  overflow: hidden;
}
.so-toast-container.top-left {
  top: 0;
  left: 0;
  align-items: flex-start;
}
.so-toast-container.top-right {
  top: 0;
  right: 0;
  align-items: flex-end;
}
.so-toast-container.top-center {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
}
.so-toast-container.bottom-left {
  bottom: 0;
  left: 0;
  align-items: flex-start;
  flex-direction: column-reverse;
}
.so-toast-container.bottom-right {
  bottom: 0;
  right: 0;
  align-items: flex-end;
  flex-direction: column-reverse;
}
.so-toast-container.bottom-center {
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  align-items: center;
  flex-direction: column-reverse;
}
.so-toast-container.center {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  align-items: center;
  justify-content: center;
  max-height: fit-content;
}
.so-toast-container.custom {
  align-items: center;
