.so-lms-result-stat-value {
  font-size: 36px;
  font-weight: 700;
  color: var(--so-text-heading);
}

.so-lms-result-stat-label {
  font-size: 0.8125rem;
  color: var(--so-text-muted);
}

.so-lms-result-actions {
  display: flex;
  justify-content: center;
  gap: 16px;
}

.so-lms-report-card {
  max-width: 1200px;
  margin: 0 auto;
}

.so-lms-report-header {
  background: var(--so-card-bg);
  border-radius: 10px;
  box-shadow: var(--so-shadow);
  padding: 24px;
  margin-bottom: 24px;
}

.so-lms-report-title {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1.375rem;
  font-weight: 700;
  color: var(--so-text-heading);
  margin-bottom: 24px;
}
.so-lms-report-title .material-icons {
  color: var(--so-accent-primary);
}

.so-lms-report-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 24px;
}

.so-lms-report-stat {
  text-align: center;
  padding: 16px;
  background: var(--so-card-hover-bg);
  border-radius: 8px;
}

.so-lms-report-stat-value {
  font-size: 32px;
  font-weight: 700;
  color: var(--so-text-heading);
  margin-bottom: 4px;
}

.so-lms-report-stat-label {
  font-size: 0.8125rem;
  color: var(--so-text-muted);
}

.so-lms-report-categories {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 24px;
}

.so-lms-report-category {
  background: var(--so-card-bg);
  border-radius: 10px;
  box-shadow: var(--so-shadow);
  overflow: hidden;
}

.so-lms-report-category-header {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  border-bottom: 1px solid var(--so-border-color);
}

.so-lms-report-category-icon {
  width: 48px;
  height: 48px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.so-lms-report-category-icon .material-icons {
  font-size: 24px;
  color: #ffffff;
}

.so-lms-report-category-info {
  flex: 1;
}

.so-lms-report-category-name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--so-text-heading);
}

.so-lms-report-category-courses {
  font-size: 0.8125rem;
  color: var(--so-text-muted);
}

.so-lms-report-grade {
  width: 48px;
  height: 48px;
  border-radius: 9999px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  font-weight: 700;
  color: #ffffff;
}
.so-lms-report-grade.grade-a {
  background: #34a853;
}
.so-lms-report-grade.grade-b {
  background: #1a73e8;
}
.so-lms-report-grade.grade-c {
  background: #f9ab00;
}
.so-lms-report-grade.grade-d {
  background: #ff6d00;
}
.so-lms-report-grade.grade-f {
  background: #ea4335;
}
.so-lms-report-grade.grade-na {
  background: #9e9e9e;
}

.so-lms-report-category-body {
  padding: 20px;
}

.so-lms-progress-circle {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 16px;
}

.so-lms-progress-ring {
  position: relative;
  width: 80px;
  height: 80px;
}
.so-lms-progress-ring svg {
  transform: rotate(-90deg);
}

.so-lms-progress-ring-bg {
  fill: none;
  stroke: var(--so-border-color);
  stroke-width: 8;
}

.so-lms-progress-ring-fill {
  fill: none;
  stroke: var(--so-accent-primary);
  stroke-width: 8;
  stroke-linecap: round;
  transition: stroke-dashoffset 0.5s ease;
}

.so-lms-progress-ring-text {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 700;
  color: var(--so-text-heading);
}

.so-lms-category-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.so-lms-category-detail {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.8125rem;
  color: var(--so-text-secondary);
}
.so-lms-category-detail .material-icons {
  font-size: 18px;
  color: var(--so-text-muted);
}

.so-lms-report-category-footer {
  padding: 16px 20px;
  border-top: 1px solid var(--so-border-color);
  display: flex;
  gap: 12px;
}

.so-lms-empty-state {
  text-align: center;
  padding: 60px 24px;
  background: var(--so-card-bg);
  border-radius: 10px;
  box-shadow: var(--so-shadow);
}

.so-lms-empty-icon {
  width: 80px;
  height: 80px;
  border-radius: 9999px;
  background: var(--so-card-hover-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
}
.so-lms-empty-icon .material-icons {
  font-size: 40px;
  color: var(--so-text-muted);
}

.so-lms-empty-title {
  font-size: 1rem;
  font-weight: 600;
  color: var(--so-text-heading);
  margin-bottom: 8px;
}

.so-lms-empty-text {
  font-size: 0.875rem;
  color: var(--so-text-secondary);
}

@media (max-width: 767px) {
  .so-lms-header-panel {
    padding: 12px;
  }
  .so-lms-header-top {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  .so-lms-page-title {
    font-size: 1.125rem;
  }
  .so-lms-page-title .material-icons {
    font-size: 24px;
  }
  .so-lms-search-bar {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 8px;
  }
  .so-lms-search-wrapper {
    flex: 1 1 100%;
    max-width: none;
    order: 1;
  }
  .so-lms-controls {
    order: 2;
    flex: 1;
  }
  .so-lms-filter-btn {
    min-width: 140px;
    padding: 6px 10px;
    font-size: 0.6875rem;
  }
  .so-lms-view-btn {
    width: 32px;
    height: 30px;
  }
  .so-lms-search-stats {
    order: 3;
    font-size: 0.6875rem;
  }
  .so-lms-course-grid {
    grid-template-columns: 1fr;
  }
  .so-lms-report-categories {
    grid-template-columns: 1fr;
  }
  .so-lms-quiz-footer {
    flex-wrap: wrap;
    gap: 16px;
  }
  .so-lms-quiz-dots {
    order: 2;
    width: 100%;
    padding: 0;
  }
}
