    }

    .#{$so-prefix}alert-link {
      color: color.adjust($so-danger, $lightness: 15%);
    }
  }

  .#{$so-prefix}alert-primary {
    background: rgba($so-primary, 0.12);
    border-color: rgba($so-primary, 0.25);

    > .material-icons,
    > .#{$so-prefix}alert-icon {
      background: rgba($so-primary, 0.2);
    }

    code {
      background: rgba($so-primary, 0.2);
      color: color.adjust($so-primary, $lightness: 15%);
    }
  }

  .#{$so-prefix}alert-primary {
    background: rgba($so-primary, 0.12);
    border-color: rgba($so-primary, 0.25);

    > .material-icons,
    > .#{$so-prefix}alert-icon {
      background: rgba($so-primary, 0.2);
    }

