      background: rgba($so-primary, 0.2);
      color: color.adjust($so-primary, $lightness: 15%);
    }

    .#{$so-prefix}alert-link {
      color: color.adjust($so-primary, $lightness: 15%);
    }
  }

  .#{$so-prefix}alert-secondary {
    background: rgba($so-secondary, 0.15);
