    > .material-icons,
    > .#{$so-prefix}alert-icon {
      background: rgba($so-secondary, 0.2);
      color: $so-grey-400;
    }

    code {
      background: rgba($so-secondary, 0.2);
      color: $so-grey-300;
    }

    .#{$so-prefix}alert-link {
