// ============================================
// HORIZONTAL CARD LAYOUT
// ============================================

.#{$so-prefix}card-horizontal {
  display: flex;
  flex-direction: row;

  > .#{$so-prefix}card-img {
    width: 200px;
    min-width: 200px;
    height: auto;
    object-fit: cover;
    border-radius: radius("lg") 0 0 radius("lg");
  }

  > .#{$so-prefix}card-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    min-width: 0;
  }

  // Stack on mobile
  @media (max-width: 576px) {
    flex-direction: column;

    > .#{$so-prefix}card-img {
      width: 100%;
      min-width: auto;
      max-height: 200px;
      border-radius: radius("lg") radius("lg") 0 0;
    }
  }
}

// ============================================
// CARD HEADER WITH AVATAR
// ============================================

.#{$so-prefix}card-header-avatar {
  display: flex;
  align-items: center;
  gap: spacing("3");

  // Use the reusable .so-avatar component for card avatars
  // Legacy support: .so-card-avatar maps to .so-avatar styles
  .#{$so-prefix}card-avatar {
    width: 40px;
    height: 40px;
    border-radius: radius("full");
    object-fit: cover;
    flex-shrink: 0;
  }

  // When using .so-avatar directly in card header
  .#{$so-prefix}avatar {
    flex-shrink: 0;
  }

  .#{$so-prefix}card-header-content {
    flex: 1;
    min-width: 0;

    .#{$so-prefix}card-title {
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .#{$so-prefix}card-subtitle {
      margin-top: spacing("1");
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}

// ============================================
// CARD ACTION AREAS
// ============================================

// Full clickable card area
.#{$so-prefix}card-action-area {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  transition: background $so-transition-fast;
  border-radius: radius("lg");

  &:hover {
    background: css-var("card-hover-bg");
  }

  &:active {
    background: alpha($so-primary, 0.1);
  }

  &:focus-visible,
  &.#{$so-prefix}focus-visible {
    outline: 2px solid css-var("accent-primary");
    outline-offset: 2px;
  }
}

// Discrete actions at bottom
.#{$so-prefix}card-actions {
  display: flex;
  align-items: center;
  gap: spacing("2");
  padding: spacing("2") spacing("4");

  &-start {
    justify-content: flex-start;
  }

  &-end {
    justify-content: flex-end;
  }

  &-between {
    justify-content: space-between;
  }

  &-center {
    justify-content: center;
  }
}

// ============================================
// CARD LOADING STATE
// Note: Skeleton styles moved to _skeleton.scss
// ============================================

.#{$so-prefix}card-loading,
.#{$so-prefix}card[data-loading="true"],
.#{$so-prefix}card[data-loading] {
  position: relative;
  pointer-events: none;

  // Overlay effect
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: css-var("card-bg");
    opacity: 0.7;
    z-index: 10;
    border-radius: radius("lg");
  }

  // Spinner
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 24px;
    height: 24px;
    margin: -12px 0 0 -12px;
    border: 3px solid css-var("border-color");
    border-top-color: css-var("accent-primary");
    border-radius: 50%;
    animation: so-card-spin 0.8s linear infinite;
    z-index: 11;
  }
}

// Disable loading when data-loading="false"
.#{$so-prefix}card[data-loading="false"] {
  pointer-events: auto;

  &::before,
  &::after {
    content: none;
  }
}

@keyframes so-card-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
