  // Ant color variants
  @each $name,
    $color
      in (
        "primary": #1890ff,
        "success": #52c41a,
        "danger": #ff4d4f,
        "warning": #faad14
      )
  {
    &.#{$so-prefix}switch-ant-#{$name} {
