// ============================================
// BUTTON LOADING STATE
// ============================================

.#{$so-prefix}btn.#{$so-prefix}loading {
  position: relative;
  color: transparent !important;
  pointer-events: none;

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 16px;
    height: 16px;
    margin: -8px 0 0 -8px;
    border: 2px solid $so-white;
    border-right-color: transparent;
    border-radius: 50%;
    animation: so-btn-spin 0.6s linear infinite;
