    color: $so-white;
  }

  // Light variants stay light style
  &.#{$so-prefix}btn-light-primary {
    background: css-var('accent-primary-light', $so-primary-light);
    color: css-var('accent-primary');
  }

  &.#{$so-prefix}btn-light-secondary {
